<template>

  <div>
      <a-page-header
    style="border: 0px solid rgb(235, 237, 240)"
    :title='ticket_tit+" . " +ticket_tit2'
    :breadcrumb="{ props: { routes } }"

  />
    <a-divider orientation="left"
      ></a-divider
    >
  <a-row >
    <a-card :bordered="true">
      <!-- <a-card-grid style="text-align: left"> -->
        <div style="text-align: left">
          {{ email_rev }}
          <a-divider type="vertical" /> nodelrd@gmail.com<br />
          {{ email_sub_des }}
          <a-divider type="vertical" /> {{ysubject}}

          <a-divider></a-divider>
          <div style="text-align: left">
            <p>user : {{ user }} 
            <a-tag color="green">{{must_be}}
            </a-tag></p>
            <p>IP : xxx.xxx.xxx.xxx <a-tag color="green">{{must_be}}</a-tag></p>
           <p> {{email_content}}: </p>
            <p>&nbsp;&nbsp;{{email_content_1}}</p>
            
          </div>
        </div>
      <!-- </a-card-grid> -->
    </a-card>
    </a-row>
    <!-- <a-spin :spinning="spinning" /> -->
    <!-- <a-button type="primary" @click="saveSetting"> Primary </a-button> -->
  </div>
</template>


<script>
import xlang from "../utils/xlang";
export default {
  data() {
    console.log(xlang.GetLang("email_revc"));
    return {
      // disabled: false,
      // loading: false,
      // value: 2,
      // desc: "QUANJU",
      user: xlang.GetUser(),
      email_rev: xlang.GetLang("email_revc").message,
      email_sub_des: xlang.GetLang("email_subject").message,
      spinning: false,
      email_content:xlang.GetLang("email_content").message,
      email_content_1:xlang.GetLang("email_content_1").message,
      ysubject:xlang.GetLang("ysubject").message,
      ticket_tit:xlang.GetLang("ticket_tit").message,
      ticket_tit2:xlang.GetLang("ticket_tit2").message,
      must_be:xlang.GetLang("must_be").message,
    };
  },
  methods: {
    saveSetting() {
      console.log("test go");
      xlang.Login(
        "",
        "",
        (function (tmp) {
          return function () {
            tmp.spinning = true;
          };
        })(this)
      );
    },
    onChange(checked) {
      console.log(`a-switch to ${checked}`);
      this.loading = true;
    },
    onChangea(e) {
      console.log("radio checked", e.target.value);
      if (e.target.value == 1) {
        this.desc = "";
      }
      if (e.target.value == 2) {
        this.desc = "PAC+";
      }
    },
  },
};
</script>

<style>
.editable-cell {
  position: relative;
}

.editable-cell-input-wrapper,
.editable-cell-text-wrapper {
  padding-right: 24px;
}

.editable-cell-text-wrapper {
  padding: 5px 24px 5px 5px;
}

.editable-cell-icon,
.editable-cell-icon-check {
  position: absolute;
  right: 0;
  width: 20px;
  cursor: pointer;
}

.editable-cell-icon {
  line-height: 18px;
  display: none;
}

.editable-cell-icon-check {
  line-height: 28px;
}

.editable-cell:hover .editable-cell-icon {
  display: inline-block;
}

.editable-cell-icon:hover,
.editable-cell-icon-check:hover {
  color: #108ee9;
}

.editable-add-btn {
  margin-bottom: 8px;
}
</style>


