<template>
  <div>

    <a-divider orientation="left">
      {{route_setting}}
    </a-divider>
    <a-spin :spinning="spinning">
      <a-card>

        <a-radio-group v-model="value" @change="onChangeMode">
          <a-radio :value="0">
            {{direct}}
          </a-radio>

          <a-radio :value="1">
            {{google_mode}}
          </a-radio>

          <a-radio :value="2" :disabled="v2">
            {{gwflist_mode}}
          </a-radio>

          <a-radio :value="3" :disabled="v3">
            {{custom_mode}}
          </a-radio>

          <a-radio :value="4" :disabled="v4">
            {{custom_mode}}+{{gwflist_mode}}
          </a-radio>

          <a-radio :value="5" :disabled="v5">
            {{global_mode}}
          </a-radio>

          <a-radio :value="6" :disabled="v5">
            {{whitelist_mode}}
          </a-radio>

        </a-radio-group>
        <a-divider>
          <!-- {{desc}} -->
        </a-divider>
        <a-card-meta>
          <template slot="description">
            {{proxy}}
            <a-divider type="vertical" />{{mode_desc}}
          </template>
        </a-card-meta>
      </a-card>
    </a-spin>
    <a-divider orientation="left" />

    <!-- <a-card > -->

    <div>
      <a-spin :spinning="spinning">
        <a-button class="editable-add-btn" @click="showModal">Add</a-button>
        <!-- <a-button class="editable-add-btn" @click="showModal"></a-button> -->
        <a-table :columns="columns" :data-source="data" bordered :pagination="false">

          <template slot="M" slot-scope="text, record, index">
            <span v-show="record.M=='1'">PROXY</span>
            <span v-show="record.M=='2'">DIRECT</span>
          </template>

          <template slot="operation" slot-scope="text, record, index">
            <div class="editable-row-operations">
              <span>
                <a-popconfirm title="Sure to Delete?" @confirm="() => cancel(record.key)">
                  <a>Delete</a>
                </a-popconfirm>
              </span>
            </div>
          </template>
        </a-table>
      </a-spin>
    </div>

    <a-modal :title="addrule" :visible="addRuleVisible" @cancel="addRuleHandCancel" :footer="null">
      <a-form-model :model="formInline">

        <a-form-model-item>
          <a-input v-model="formInline.rule" placeholder="If you want proxy google.com. Please input google.com">
          </a-input>
        </a-form-model-item>

        <a-form-model-item>
          <a-select v-model="formInline.select" placeholder="please select mode">
            <a-select-option value="1">
              PROXY
            </a-select-option>
            <a-select-option value="2">
              DIRECT
            </a-select-option>
          </a-select>
        </a-form-model-item>

        <a-form-model-item>
          <a-button type="primary" @click="()=>{addRuleHandleSubmit(this)}">
            Add
          </a-button>

        </a-form-model-item>

      </a-form-model>
    </a-modal>

  </div>
</template>




<script>
import xlang from "../utils/xlang";

const columns = [
  {
    title: "Domian",
    dataIndex: "S",
    width: "60%",
    scopedSlots: { customRender: "S" },
  },
  {
    title: "Mode",
    dataIndex: "M",
    width: "15%",
    scopedSlots: { customRender: "M" },
  },
  {
    title: "operation",
    dataIndex: "operation",
    scopedSlots: { customRender: "operation" },
  },
];

let data = [];
var cl = localStorage.getItem("custom_list");

if (typeof cl == "undefined" || cl == null || cl.length == 0) {
} else {
  data = JSON.parse(cl);
}

export default {
  data() {
    this.cacheData = data.map((item) => ({ ...item }));
    let vs = xlang.getVipSet();
    console.log(~vs.v2, vs.v2);
    return {
      v2: !vs.v2,
      v3: !vs.v3,
      v4: !vs.v4,
      v5: !vs.v5,
      spinning: false,
      value: xlang.getModel(),
      desc: "QUANJU",
      visible: false,
      data,
      columns,
      editingKey: "",
      route_setting: xlang.GetLang("route_setting").message,
      direct: xlang.GetLang("direct").message,
      global_mode: xlang.GetLang("global_mode").message,
      whitelist_mode: xlang.GetLang("whitelist_mode").message,
      custom_mode: xlang.GetLang("custom_mode").message,
      gwflist_mode: xlang.GetLang("gwflist_mode").message,
      google_mode: xlang.GetLang("google_mode").message,
      mode_desc: xlang.GetLang("web_site_1").message,
      proxy: xlang.GetLang("proxy").message,
      formInline: {
        rule: "",
        select: "1",
      },
      addrule: xlang.GetLang("addrule").message,
      addRuleVisible: false,
    };
  },
  methods: {
    addRuleHandleSubmit() {
      if (this.v3 == true) {
        this.$message.warning(xlang.GetLang("popnevip").message);
        return;
      }
      if (this.data.length > 0) {
        this.data.unshift({
          key: this.data[0].key + 1,
          S: this.formInline.rule,
          M: this.formInline.select,
        });
      } else {
        this.data.unshift({
          key: 0,
          S: this.formInline.rule,
          M: this.formInline.select,
        });
      }

      localStorage.setItem("custom_list", JSON.stringify(this.data));
      console.log(this.formInline);
      this.addRuleVisible = false;
      if (this.value == 3 || this.value == 4) {
        this.spinning = true;
        xlang.setModel(
          this.value,
          (function (ddc) {
            return function (b) {
              if (b == 0) {
                ddc.$message.success("This is a success message");
              }
              if (b == 1) {
                ddc.$message.warning("connect failed");
              }
              if (b == 2) {
                ddc.$message.warning("Need Login");
              }
              ddc.spinning = false;
            };
          })(this)
        );
      }
    },
    addRuleHandCancel() {
      this.addRuleVisible = false;
    },
    showModal() {
      this.addRuleVisible = true;
    },
    // handleOk(e) {
    //   console.log(e);
    //   this.visible = false;
    // },
    onChange(checked) {
      console.log(`a-switch to ${checked}`);
      this.loading = true;
    },
    onChangeMode(e) {
      //         <a-radio :value="1">
      //     {{google_mode}}
      //   </a-radio>

      //   <a-radio :value="2">
      //     {{gwflist_mode}}
      //   </a-radio>

      //   <a-radio :value="3">
      //     {{custom_mode}}
      //   </a-radio>

      //   <a-radio :value="4">
      //     {{custom_mode}}+{{gwflist_mode}}
      //   </a-radio>

      //   <a-radio :value="5">
      //     {{global_mode}}
      //   </a-radio>
      // console.log("radio checked", e.target.value);
      this.spinning = true;
      if (e.target.value == 1) {
        this.mode_desc = xlang.GetLang("web_site_1").message;
      }
      if (e.target.value == 2) {
        this.mode_desc = xlang.GetLang("web_site_2").message;
      }
      if (e.target.value == 3) {
        this.mode_desc = xlang.GetLang("custom_mode_desc").message;
      }
      if (e.target.value == 4) {
        this.mode_desc =
          xlang.GetLang("custom_mode_desc").message +
          "+" +
          xlang.GetLang("web_site_2").message;
      }
      if (e.target.value == 5) {
        this.mode_desc = xlang.GetLang("global_mode_desc").message;
      }

      if (e.target.value == 6) {
        this.mode_desc = xlang.GetLang("whitelistDesc_mode").message;
      }

      xlang.setModel(
        e.target.value,
        (function (ddc) {
          return function (b) {
            if (b == 0) {
              ddc.$message.success("This is a success message");
            }
            if (b == 1) {
              ddc.$message.warning("connect failed");
            }
            if (b == 2) {
              ddc.$message.warning("Need Login");
            }
            ddc.spinning = false;
          };
        })(this)
      );
    },
    cancel(key) {
      const dataSource = [...this.data];
      console.log("delete key:", key, dataSource);
      this.data = dataSource.filter((item) => item.key !== key);
      localStorage.setItem("custom_list", JSON.stringify(this.data));
      console.log(this.value);
      if (this.value == 3 || this.value == 4) {
        this.spinning = true;
        xlang.setModel(
          this.value,
          (function (ddc) {
            return function (b) {
              if (b == 0) {
                ddc.$message.success("This is a success message");
              }
              if (b == 1) {
                ddc.$message.warning("connect failed");
              }
              if (b == 2) {
                ddc.$message.warning("Need Login");
              }
              ddc.spinning = false;
            };
          })(this)
        );
      }
    },
  },
};
</script>

<style>
.editable-cell {
  position: relative;
}

.editable-cell-input-wrapper,
.editable-cell-text-wrapper {
  padding-right: 24px;
}

.editable-cell-text-wrapper {
  padding: 5px 24px 5px 5px;
}

.editable-cell-icon,
.editable-cell-icon-check {
  position: absolute;
  right: 0;
  width: 20px;
  cursor: pointer;
}

.editable-cell-icon {
  line-height: 18px;
  display: none;
}

.editable-cell-icon-check {
  line-height: 28px;
}

.editable-cell:hover .editable-cell-icon {
  display: inline-block;
}

.editable-cell-icon:hover,
.editable-cell-icon-check:hover {
  color: #108ee9;
}

.editable-add-btn {
  margin-bottom: 8px;
}
</style>


