<script>
import xlang from "../utils/xlang";
import jrQrcode from "jr-qrcode";
export default {
  data() {
    let imgBase64 = jrQrcode.getQrBase64("text", {
      padding: 10, // 10px
      width: 256, // 256px
      height: 256, // 256px
      correctLevel: QRErrorCorrectLevel.H, // level
      reverse: false, // 
      background: "#ffffff", // 
      foreground: "#000000", // 
    });
    const istmp = xlang.CheckIsTmpUser(xlang.GetUser());
    let vip = false;
    let web1 = false;
    let web2 = false;
    let web3 = false;
    console.log("user is tmp", istmp, xlang.GetUser());
    if (istmp) {
      vip = false;
      web1 = false;
      web2 = false;
      web3 = false;
    } else {
      let xuserinfo = localStorage.getItem("xuserInfo");
      let yxuserinfo = JSON.parse(xuserinfo);
      vip = yxuserinfo.data.vip;
      web1 = yxuserinfo.data.web1;
      web2 = yxuserinfo.data.web2;
      web3 = yxuserinfo.data.web3;
    }
    console.log("user is tmp finish", istmp);

    let validateEmail = (rule, value, callback) => {
      console.log(rule, value);
      var verify = /^\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/;
      if (!verify.test(value)) {
        callback(new Error("Please input a Email"));
        return;
      }

      callback();
    };

    let validateRePass = (rule, value, callback) => {
      console.log(
        rule,
        value,
        this.formInline.repassword,
        value != this.formInline.repassword
      );
      // var verify = /^\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/;
      if (value != this.formInline.password) {
        callback(new Error("The two passwords are inconsistent"));
        return;
      }
      callback();
    };
    let xHaveMessage = localStorage.getItem("xHaveMessage");
    if (xHaveMessage == null || typeof xHaveMessage == "undefined") {
      xHaveMessage = "";
    }

    let xHaveDay = localStorage.getItem("xHaveDay");
    if (xHaveDay == null || typeof xHaveDay == "undefined") {
      xHaveDay = "";
    }

    return {
      qrcodeImg: imgBase64,
      restart_system_open_vip: xlang.GetLang("restart_system_open_vip").message,
      formInline: {
        user: "",
        password: "",
        repassword: "",
      },
      cardForm: {
        card: "",
      },
      // card: "",
      cardloading: false,
      rules: {
        user: [
          {
            required: true,
            message: "Please input user name",
            trigger: ["change", "blur"],
          },
          {
            min: 3,
            max: 24,
            message: "Length should be 3 to 24",
            trigger: ["change", "blur"],
          },
          { validator: validateEmail, trigger: ["change", "blur"] },
        ],
        password: [
          {
            required: true,
            message: "Please input password",
            trigger: ["change", "blur"],
          },
          {
            min: 3,
            max: 24,
            message: "Length should be 3 to 24",
            trigger: ["change", "blur"],
          },
        ],
        repassword: [
          {
            required: true,
            message: "Please input password",
            trigger: ["change", "blur"],
          },
          {
            min: 3,
            max: 24,
            message: "Length should be 3 to 24",
            trigger: ["change", "blur"],
          },
          { validator: validateRePass, trigger: ["change", "blur"] },
        ],
      },
      rulesForLogin: {
        user: [
          {
            required: true,
            message: "Please input user name",
            trigger: ["change", "blur"],
          },
          {
            min: 3,
            max: 24,
            message: "Length should be 3 to 24",
            trigger: ["change", "blur"],
          },
          { validator: validateEmail, trigger: ["change", "blur"] },
        ],
        password: [
          {
            required: true,
            message: "Please input password",
            trigger: ["change", "blur"],
          },
          {
            min: 3,
            max: 24,
            message: "Length should be 3 to 24",
            trigger: ["change", "blur"],
          },
        ],
      },
      handleCardSubmit(vthis) {
        vthis.cardloading = true;
        console.log(vthis.cardForm);
        xlang.Pay(vthis.cardForm.card, function (t, s) {
          vthis.cardloading = false;
          console.log(t, s, "pay callback");
          if (t == false) {
            vthis.$message.error(xlang.GetLang("network_error").message);
          } else {
            if (s.c == "1") {
              vthis.$message.success(
                xlang.GetLang("open_vip").message +
                  xlang.GetLang("success").message
              );
              vthis.restartDisable = true;
              vthis.restartMessage = 3;
              var myVar = setInterval(function () {
                if (vthis.restartMessage == 0) {
                  clearInterval(myVar);
                  chrome.runtime.reload();
                }
                vthis.restartMessage--;
              }, 1000);
            }
            if (s.c == "4") {
              vthis.$message.error(xlang.GetLang("relogin").message);
            }
          }
        });
      },
      look() {
        let routeData = this.$router.resolve({
          path: "/look",
        });
        window.open(routeData.href, "_blank");
      },
      handleSignSubmit: function (dda) {
        let hv = this;
        dda.$refs.ruleForm.validate((valid) => {
          if (valid) {
            // alert('submit!');
            xlang.Reg(
              this.formInline.user,
              this.formInline.password,
              function (ok, userinfo) {
                if (ok) {
                  //update userinfo
                  console.log("reg isdddd", userinfo);
                  if (userinfo.code == 200) {
                    hv.$message.success("success", 10);
                    hv.signvisible = false;
                    hv.web1 = userinfo.data.web1;
                    hv.username = userinfo.data.u;
                    hv.web2 = userinfo.data.web2;
                    hv.web3 = userinfo.data.web3;
                    hv.vip = userinfo.data.vip;
                    hv.restartDisable = true;
                    hv.restartMessage = 3;
                    var myVar = setInterval(function () {
                      if (hv.restartMessage == 0) {
                        clearInterval(myVar);
                        chrome.runtime.reload();
                      }
                      hv.restartMessage--;
                    }, 1000);
                  } else {
                    hv.$message.error(
                      xlang.GetLang(userinfo.message).message,
                      10
                    );
                  }
                } else {
                  hv.$message.error(xlang.GetLang("network_error").message, 10);
                  return false;
                }
              }
            );
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
        console.log(dda.$refs.ruleForm);
        console.log(this.formInline);
      },

      handleLoginSubmit: function (dda) {
        let hv = this;
        dda.$refs.ruleForm.validate((valid) => {
          if (valid) {
            console.log(this.formInline, "xxx callback");
            xlang.Login(
              hv.formInline.user,
              hv.formInline.password,
              function (t, r) {
                if (t == true) {
                  if (r.code == "200") {
                    hv.$message.success(xlang.GetLang("success").message, 10);
                    hv.signvisible = false;
                    hv.web1 = r.data.web1;
                    hv.username = r.data.u;
                    hv.web2 = r.data.web2;
                    hv.web3 = r.data.web3;
                    hv.vip = r.data.vip;
                    hv.restartDisable = true;
                    hv.restartMessage = 3;
                    var myVar = setInterval(function () {
                      if (hv.restartMessage == 0) {
                        clearInterval(myVar);
                        chrome.runtime.reload();
                      }
                      hv.restartMessage--;
                    }, 1000);
                  } else {
                    hv.$message.error(xlang.GetLang("failed").message, 10);
                  }
                } else {
                  hv.$message.error(xlang.GetLang("network_error").message, 10);
                }
              }
            );
          } else {
            return false;
          }
        });
        console.log(dda.$refs.ruleForm);
        console.log(this.formInline);
      },

      handleOk: function (e, e2, e3) {
        console.log("handleOk", e);
        return false;
      },
      loginvisible: false,
      signvisible: false,
      buyVisible: false,
      restartDisable: false,
      restartMessage: 3,
      confirmLoginLoading: false,
      confirmSignLoading: false,
      isTmp: xlang.CheckIsTmpUser(localStorage.getItem("username")),
      account: xlang.GetLang("account").message,
      free_network_speed: xlang.GetLang("free_network_speed").message,
      vip_network_speed: xlang.GetLang("vip_network_speed").message,
      web_site_1: xlang.GetLang("web_site_1").message,
      web_site_2: xlang.GetLang("web_site_2").message,
      web_site_3: xlang.GetLang("web_site_3").message,
      system_message: xlang.GetLang("system_message").message,
      free_network_traffic: xlang.GetLang("free_network_traffic").message,
      vip_network_traffic: xlang.GetLang("vip_network_traffic").message,
      web_site: xlang.GetLang("web_site").message,
      mo: xlang.GetLang("month").message,
      traffic: xlang.GetLang("traffic").message,
      login: xlang.GetLang("login").message,
      re_login: xlang.GetLang("re_login").message,
      sgin_up: xlang.GetLang("sgin_up").message,
      no_limit: xlang.GetLang("no_limit").message,
      enabel: xlang.GetLang("enabel").message,
      buy_now: xlang.GetLang("buy_now").message,
      or: xlang.GetLang("or").message,
      status: xlang.GetLang("status").message,
      s202205: xlang.GetLang("202205").message,
      haveMessage: xHaveMessage,
      xHaveDay: xHaveDay,
      open_vip: xlang.GetLang("open_vip").message,
      balance: xlang.GetLang("balance").message,
      disabled: false,
      loading: false,
      value: 2,
      desc: "QUANJU",
      network_out: xlang.GetLang("network_out").message,
      username: xlang.GetUser(),
      vip: vip,
      web1: web1,
      web2: web2,
      web3: web3,
      balance_num: 0,
    };
  },
  methods: {
    showBuyModal() {
      this.buyVisible = true;
    },
    showLoginModal() {
      this.loginvisible = true;
    },
    handLoginOk(e) {
      this.ModalText = "The modal will be closed after two seconds";
      this.confirmLoginLoading = true;
      setTimeout(() => {
        this.loginvisible = false;
        this.confirmLoginLoading = false;
      }, 2000);
    },
    handLoginCancel(e) {
      console.log("Clicked cancel button");
      this.loginvisible = false;
    },

    showSignModal() {
      this.signvisible = true;
    },
    handSignOk(e) {
      this.ModalText = "The modal will be closed after two seconds";
      this.confirmLoginLoading = true;

      setTimeout(() => {
        this.signvisible = false;
        this.confirmSignLoading = false;
      }, 2000);
    },
    handSignCancel(e) {
      console.log("Clicked cancel button");
      this.signvisible = false;
    },
    handBuyCancel(e) {
      // console.log('Clicked cancel button');
      this.buyVisible = false;
    },

    onChange(checked) {
      console.log(`a-switch to ${checked}`);
      this.loading = true;
    },
    onChangea(e) {
      console.log("radio checked", e.target.value);
      if (e.target.value == 1) {
        this.desc = "";
      }
      if (e.target.value == 2) {
        this.desc = "PAC";
      }
    },
  },
};
