
var nozh = require("../../_locales/en/messages.json")
var zhcn = require("../../_locales/zh_CN/messages.json")
// var langa = chrome.i18n.getUILanguage()
export default {
    debug: false,
    GetLang(name) {
        if (this.debug == true) {
            let lang = 'zh_CN'//chrome.i18n.getUILanguage()
            let data = zhcn
            if (lang != "zh_CN") {
                data = nozh
            }
            if (name == null) {
                console.log(name)
                return 'null'
            }

            // console.log(data[name])
            return data[name]
        }

        return { message: chrome.i18n.getMessage(name) }

    },
    SetPac(mode, callback) {
        if (this.debug == true) {
            callback(true)
            return
        }
        var bg = chrome.extension.getBackgroundPage();
        bg.SetPac(mode, callback)
    },
    GetUser() {

        if (this.debug == true) {
            return '0x9a53b09d-dd8a-4463-8b24-dbcaa2015744'
        }
        // return 'user'
        // if (this.CheckIsTmpUser(localStorage.getItem("username"))) {
        //     return 'user'
        // }
        return localStorage.getItem("username")
    },
    Login(user, password, callback) {
        // console.log(user, password, callback)
        // callback()
        if (this.debug == true) {
            localStorage.setItem("username", "0x9a53b09d-dd8a-4463-8b24-dbcaa2015744")
            callback(true)
            return
        }
        var bg = chrome.extension.getBackgroundPage();
        // console.log("test bg function", user, password, callback, bg)
        bg.login(user, password, callback)
    },
    Reg(user, password, callback) {
        // console.log(user, password, callback)
        // callback()
        if (this.debug == true) {
            localStorage.setItem("username", "nodelrd@gmail.com")
            callback(true)
            return
        }
        var bg = chrome.extension.getBackgroundPage();
        console.log("test bg function", user, password, callback, bg)
        bg.reg(user, password, callback)
    },
    CheckIsTmpUser(username) {
        // return true
        // if(this.debug==true){
        if (username.length > 0) {
            let userdata = username.split("-")
            if (userdata.length == 5 && userdata[0].startsWith("0x") && userdata[2].startsWith("4")) {
                return true
            }
        }
        return false
        // }
        // var bg = chrome.extension.getBackgroundPage();
        // // console.log("test bg function", user, password, callback, bg)
        // return bg.CheckIsTmpUser(user)
    },
    getModel() {
        let mode = 1
        let vmode = localStorage.getItem("xmode");
        if (typeof vmode == "undefined" || vmode == null || vmode.length == 0) {

        } else {
            mode = parseInt(vmode)
        }

        return mode
    },
    setModel(mode, callback) {
        localStorage.setItem("xmode", mode);
        if (this.debug == true) {
            callback(0)
            return
        }

        var bg = chrome.extension.getBackgroundPage();

        bg.SettingFromUI(mode, callback)

        // return mode
    },
    getVipSet() {
        const istmp = this.CheckIsTmpUser(this.GetUser())
        let vip = false
        let web1 = false
        let web2 = false
        let web3 = false
        // console.log("user is tmp", istmp, xlang.GetUser())
        if (istmp) {
            vip = false
            web1 = false
            web2 = false
            web3 = false
        } else {
            let xuserinfo = localStorage.getItem("xuserInfo")
            let yxuserinfo = JSON.parse(xuserinfo)
            vip = yxuserinfo.data.vip
            web1 = yxuserinfo.data.web1
            web2 = yxuserinfo.data.web2
            web3 = yxuserinfo.data.web3
        }

        let vs = {
            "v1": true,
            "v2": false,
            "v3": false,
            "v4": false,
            "v5": false
        }
        if (web2 == true) {
            vs.v2 = true
        }
        if (web3 == true) {
            vs.v3 = true
            vs.v4 = true
            vs.v5 = true
            vs.v2 = true
        }
        return vs
    },
    Pay(encode, callback) {
        if (this.debug == false) {
            var bg = chrome.extension.getBackgroundPage();
            bg.Pay(encode, callback)
        } else {
            callback(true, { c: 1 })
        }
    }
}


