<template>
  <div id="app">
    <!--  -->
    <div v-if="type === 'main'">
      <a-spin :spinning="initspinning" tip="Connect Server">
        <a-layout id="components-layout-demo-custom-trigger">
          <a-layout-sider v-model="collapsed" :trigger="null" collapsible style="min-height: 1920px">
            <div class="logo" />
            <a-menu theme="dark" mode="inline" :default-selected-keys="['1']">
              <a-menu-item key="1" @click="changeMenu('/userinfo')">
                <a-icon type="user" />
                <span>{{ menu_user }}</span>
              </a-menu-item>
              <a-menu-item key="2" @click="changeMenu('/setting')">
                <a-icon type="setting" />
                <span>{{ menu_setting }}</span>
              </a-menu-item>
              <a-menu-item key="ticket" @click="changeMenu('/ticket')">
                <a-icon type="question-circle" />
                <span>{{ menu_ticket }}</span>
              </a-menu-item>
            </a-menu>
          </a-layout-sider>
          <a-layout>
            <a-layout-header style="background: #fff; padding: 0">
              <a-icon class="trigger" :type="collapsed ? 'menu-unfold' : 'menu-fold'" @click="() => (collapsed = !collapsed)" />
            </a-layout-header>
            <a-layout-content :style="{
              margin: '24px 16px',
              padding: '24px',
              background: '#fff',
              minHeight: '280px',
            }">
              <router-view></router-view>
            </a-layout-content>
          </a-layout>
        </a-layout>
      </a-spin>
    </div>

    <!--  -->
    <a-row type="flex" justify="center" align="middle" style="height: 600px" v-if="type === 'login'">
      <a-spin :spinning="spinning">
        <a-card title="Login system" style="width: 300px">
          <a-form-model :model="formInline" @submit="handleLogin" @submit.native.prevent>
            <a-form-model-item>
              <a-input v-model="formInline.user" placeholder="Email">
                <a-icon slot="prefix" type="user" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-model-item>
            <a-form-model-item>
              <a-input v-model="formInline.password" type="password" placeholder="Password">
                <a-icon slot="prefix" type="lock" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-model-item>
            <a-form-model-item>
              <a-button type="primary" html-type="submit" :disabled="formInline.user === '' || formInline.password === ''">
                Log in
              </a-button>
              or
              <a href="#" @click="toRegPage">Sign up here.</a>
            </a-form-model-item>
          </a-form-model>
        </a-card>
      </a-spin>
    </a-row>

    <!--  -->
    <a-row type="flex" justify="center" align="middle" style="height: 600px" v-if="type === 'reg'">
      <a-spin>
        <a-card title="Create an account" style="width: 300px">
          <a-form-model :model="formInline" @submit="handleSubmit" @submit.native.prevent>
            <a-form-model-item>
              <a-input v-model="formInline.user" placeholder="Enter your email">
                <a-icon slot="prefix" type="user" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-model-item>
            <a-form-model-item>
              <a-input v-model="formInline.password" type="password" placeholder="Enter your password">
                <a-icon slot="prefix" type="lock" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-model-item>

            <a-form-model-item>
              <a-input v-model="formInline.repassword" type="password" placeholder="Confirm your password">
                <a-icon slot="prefix" type="lock" style="color: rgba(0, 0, 0, 0.25)" />
              </a-input>
            </a-form-model-item>

            <a-form-model-item>
              <a-button type="primary" html-type="submit" :disabled="formInline.user === '' || formInline.password === ''">
                register
              </a-button>
              or
              <a @click="toLoginPage">login</a>
            </a-form-model-item>
          </a-form-model>
        </a-card>
      </a-spin>
    </a-row>

    <!-- popup  -->
    <div v-if="type === 'popup'">
      <a-spin :spinning="initspinning" tip="Connect Server">
        <a-card title="setting">
          <a slot="extra" href="#/userinfo" target="_blank">more</a>
          <a-select :default-value="1" :value="vmode" style="width: 100%" @change="changeMode">
            <a-select-option :value="0"> {{direct}} </a-select-option>
            <a-select-option :value="1"> {{google_mode}} </a-select-option>
            <a-select-option :value="2" :disabled="v2"> {{gwflist_mode}} </a-select-option>
            <a-select-option :value="3" :disabled="v3"> {{custom_mode}} </a-select-option>
            <a-select-option :value="4" :disabled="v4"> {{custom_mode}}+{{gwflist_mode}} </a-select-option>
            <a-select-option :value="5" :disabled="v4"> {{global_mode}} </a-select-option>
            <a-select-option :value="6" :disabled="v4"> {{whitelist_mode}} </a-select-option>

          </a-select>
        </a-card>
      </a-spin>
    </div>
  </div>
</template>


<script>
import xlang from "./utils/xlang";

export default {
  meta: {
    navHide: true,
    menuName: "2",
    sort: 1,
  },
  data() {
    var typea = "login";
    let vs = xlang.getVipSet();
    // console.log(~vs.v2, vs.v2);
    // return {

    if (
      localStorage.getItem("username") != null &&
      localStorage.getItem("username").length > 0
    ) {
      console.log("login");
      if (this.$route.fullPath == "/popup") {
        typea = "popup";
      } else {
        typea = "main";
      }
    }

    if (this.$route.fullPath == "/login") {
      typea = "login";
    }
    let initspinning = true;
    let xinit = localStorage.getItem("init");
    if (xinit != null && xinit.length > 0) {
      initspinning = false;
    } else {
      // setTimeout;
      if (this.xinitTimer == null) {
        var xthis = this;
        this.xinitTimer = setInterval(function () {
          let xinit = localStorage.getItem("init");
          if (xinit != null && xinit.length > 0) {
            clearInterval(xthis.xinitTimer);
            xthis.xinitTimer = null;
            xthis.initspinning = false;
          }
        }, 1000);
      }
    }

    return {
      initspinning: initspinning,
      v2: !vs.v2,
      v3: !vs.v3,
      v4: !vs.v4,
      v5: !vs.v5,
      xinitTimer: null,
      vmode: xlang.getModel(),
      direct: xlang.GetLang("direct").message,
      global_mode: xlang.GetLang("global_mode").message,
      whitelist_mode: xlang.GetLang("whitelist_mode").message,
      custom_mode: xlang.GetLang("custom_mode").message,
      gwflist_mode: xlang.GetLang("gwflist_mode").message,
      google_mode: xlang.GetLang("google_mode").message,
      spinning: false,
      collapsed: false,
      type: typea,
      menu_user: xlang.GetLang("menu_user").message,
      menu_setting: xlang.GetLang("menu_setting").message,
      menu_ticket: xlang.GetLang("menu_ticket").message,
      formInline: {
        user: "",
        password: "",
        repassword: "",
      },
    };
  },
  methods: {
    //
    changeMenu(route) {
      this.$router.push({ path: route });
    },
    handleLogin(e) {
      this.spinning = true;
      // console.log(e)
      // xlang.Login()
      // console.log(this.formInline);
      // localStorage.setItem("fresh","user");
      xlang.Login(
        this.formInline.user,
        this.formInline.password,
        (function (tmp) {
          return function (isOk) {
            if (isOk) {
              tmp.$message.success("login success", 3);
              if (
                tmp.$route.query != null &&
                tmp.$route.query.toPath == "/popup"
              ) {
                tmp.type = "popup";
              } else {
                tmp.type = "main";
              }
              if (tmp.$route.query != null && tmp.$route.query.toPath != null) {
                this.$router.push({
                  path: this.$route.query.toPath,
                });
              }
            }
            tmp.spinning = false;
          };
        })(this)
      );

      console.log(this.$route.fullPath);
      console.log(this.$route);
    },
    toLoginPage(e) {
      this.type = "login";
    },
    toRegPage(e) {
      this.type = "reg";
    },
    handleChange(e) {
      // this.type = 'login'
    },
    //
    changeMode(e) {
      console.log(e);
      this.vmode = e;
      xlang.setModel(
        e,
        (function (ddc) {
          return function (b) {
            if (b == 0) {
              ddc.$message.success("success");
            }
            if (b == 1) {
              ddc.$message.warning("connect failed");
            }
            if (b == 2) {
              ddc.$message.warning("Need Login");
            }
            ddc.spinning = false;
          };
        })(this)
      );
    },
  },
};
</script>
<style>
#components-layout-demo-custom-trigger .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}

#components-layout-demo-custom-trigger .trigger:hover {
  color: #1890ff;
}

#components-layout-demo-custom-trigger .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}
</style>





